<template>
  <v-card class="mx-auto d-flex align-center justify-center pa-3" dark>
    
    
    <v-list-item three-line>
      <v-list-item-content>
        <v-list-item-title color="primary" block dark>
          <div class="headline mb-1 text-center" color="primary">{{header}}</div>
        </v-list-item-title>
        <v-list-item-action-text>
          <div class="headline text-center pt-2" style="color:white">{{employees.length}}</div>
        </v-list-item-action-text>
        <v-list-item-action-text class="text-center pt-3" style="color: white; font-size: 16px">
          <v-btn
            @click="isDetailsShown = !isDetailsShown"
          >{{isDetailsShown ? 'Hide' : 'Show'}} details</v-btn>

          <v-dialog v-model="isDetailsShown">
            <v-card dark>
              <Detailed v-bind:persons="employees" v-bind:showClearButton="true" v-bind:userType="this.userType" />
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="white" outlined text @click="isDetailsShown = false">Close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-list-item-action-text>
        <v-list-item-action-text class="text-center pt-3 text-h6">{{this.date}}</v-list-item-action-text>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
import Detailed from "../tables/Detailed";
export default {
  props: ["header", "employees", "date", "userType"],
  data() {
    return { isDetailsShown: false };
  },
  components: {
    Detailed,
  },
};
</script>